:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --dark-blue: hsl(240, 38%, 20%);
  --grayish-blue: hsl(240, 18%, 77%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
}

main {
  position: relative;
  height: 100vh;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

p {
  font-size: rem(32);
  font-weight: 300;
  color: var(--dark-blue);
}

.bg-pattern {
  background-image: url("/images/pattern-bg.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  height: 50vh;
  padding: 1.5625rem;
}

.slides {
  position: relative;
  overflow: hidden;
  width: 12.5rem;
  height: 14.0625rem;
  border-radius: 0.5rem;
  margin: 0 auto;
}

.slide-container {
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  transition: all 0.25s ease-in-out;
}
.slide-container img {
  width: auto;
  height: 100%;
}

.wrapper {
  display: flex;
  flex-direction: column;
}

.profile {
  position: relative;
}

.nav-buttons {
  position: absolute;
  bottom: -1.25rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  padding: 0.625rem;
  border-radius: 1.875rem;
  box-shadow: 3px 10px 25px 0px rgba(0, 0, 0, 0.3);
  width: 5rem;
}
.nav-buttons img {
  width: 0.9375rem;
  height: 0.9375rem;
}

.testimonial {
  height: 50vh;
}

.slides-text {
  position: relative;
}

.text {
  position: absolute;
  background-image: url(/images/pattern-quotes.svg);
  background-repeat: no-repeat;
  background-size: 4.6875rem auto;
  background-position: top center;
  max-width: 90%;
  margin: 0.9375rem auto 0;
  padding: 1.25rem 1.5625rem;
  text-align: center;
}
.text .name {
  font-size: 1rem;
  font-weight: 700;
  margin-bottom: 0rem;
}
.text .job {
  margin: 0.3125rem 0;
  font-weight: 500;
  color: var(--grayish-blue);
}

.wave img {
  width: 15.625rem;
  height: auto;
}

.slide {
  padding: 1.25rem;
}